.guias-div-1{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}


.guias-nav-1{
    background-image: linear-gradient( 178.5deg,  rgba(100,129,255,1) 2.7%, rgba(2,36,184,1) 98% );   
    display: flex;
    flex-direction: row;
    height: auto;
    width: auto;
    justify-content: center;
    align-self: center;
    padding: 5px;
    margin-bottom: 5px;
    border-radius: 2%;
    opacity: 0;
}

.guias-nav-1:hover{
    opacity: 1;
}

.guias-texto-1{
    color: white;
    font-size: 24px;
    margin: 3px;
    padding: 3px;
    justify-content: center;
    display: flex;
    justify-self: center;
    align-self: center;
}

.guias-texto-1:hover{
    color: black;
    font-weight: 600;
    background-color: dodgerblue;
    border-radius: 10%;
}

.guias-texto-2{
    color:white;
    font-size: 12px;
    background: #0f0c29;
    margin: 10%;  
}

.guias-texto-3{
    color:black;
    font-size: 36px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.guias-texto-4{
    width: 920px; 
    display: flex;
    align-items: center;
    justify-content: center;
  
}

.guias-div-perfil{
    margin: 12px;
    padding: 2px;
    position: fixed;
    top: 0;
    right: 0;
}

.guias-div-2{
    padding: 12px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row-reverse;
    width: 85%;
    border-radius: 1%;
}

.guias-div-3{
    display: flex;
    align-content: center;
    justify-content: center;
    flex-direction: column;
    padding: 2px;
    margin: 10px;
}

.guias-div-4{
   background-color: white;
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   padding: 12px;
   max-height: 550px;

}

.guias-div-5{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 6px;
    margin-bottom: 12px;
    padding: 6px;
    width:50%;
}

.guias-div-6{
     padding: 12px;
     display: flex;
     align-items: center;
     justify-content: center;
     flex-direction: column;
     max-height: 550px;
     overflow-y: scroll;

}

.guias-div-7{
    background-color: mistyrose;
     padding: 12px;
     display: flex;
     align-items: center;
     justify-content: center;
     flex-direction: column;
     margin: 2px;
     border-style: dotted;
}

.guias-image-1{
    width:80px;
    height:80px;
    object-fit:cover;
    border-radius:50%;
    border:3px solid dodgerblue  ;
    position: relative;
}

.guias-label-1{
    color: black;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    margin: 0.5px;
    background-color: mediumturquoise;
}

.guias-label-2{
    color: black;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    margin: 0.5px;
    background-color: lawngreen;
}

.guias-label-3{
    width: 100%;
}