.App-div-1{  
    position: absolute;
    height: 100% ! important;
    width: 100% ! important;

    background-image: url('./estrellas.svg');
    background-size: cover;
    background-repeat: repeat;
  
    -webkit-animation:20s scroll infinite linear; 
    -moz-animation:20s scroll infinite linear; 
    -o-animation:20s scroll infinite linear;
    -ms-animation:20s scroll infinite linear;
    animation:20s scroll infinite linear; 

}

.App-div-2{  
    position: absolute;
    height: 100% ! important;
    width: 100% ! important;
    min-height: 690px;
    min-width: 990px;
    background-image: url('./imagenes/asd.svg');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: bottom left;
}

.App-div-3{

    position: absolute;
    height: 100% ! important;
    width: 100% ! important;
    background-image: url('./estrellas.svg');
    background-size: contain;
    background-repeat: repeat;
    -webkit-animation:10s scroll infinite linear; 
    -moz-animation:10s scroll infinite linear; 
    -o-animation:10s scroll infinite linear;
    -ms-animation:10s scroll infinite linear;
    animation:10s scroll infinite linear; 
}