.estadisticas-div-1{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.estadisticas-nav-1{
    background-image: linear-gradient( 178.5deg,  rgba(100,129,255,1) 2.7%, rgba(2,36,184,1) 98% );   
    display: flex;
    flex-direction: row;
    height: auto;
    width: auto;
    justify-content: center;
    align-self: center;
    padding: 5px;
    margin-bottom: 5px;
    border-radius: 2%;
    opacity: 0;
}

.estadisticas-nav-1:hover{
    opacity: 1;
}

.estadisticas-texto-1{
    color: white;
    font-size: 24px;
    margin: 3px;
    padding: 3px;
    justify-content: center;
    display: flex;
    justify-self: center;
    align-self: center;
}

.estadisticas-texto-2{
    color:white;
    font-size: 12px;
    background: #0f0c29;
    margin: 10%;  
}

.estadisticas-texto-3{
    color: black;
    font-size: 36px;
    
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.estadisticas-div-perfil{
    margin: 12px;
    padding: 2px;
    position: fixed;
    top: 0;
    right: 0;
}

.estadisticas-div-2{
    background-color: white;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: center;
    width: 80%;
    justify-self: center;
    opacity: 0.95;

}

.estadisticas-div-3{
    margin: 12px;
    padding: 5px;
}

.estadisticas-div-4{
    margin: 6px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.estadisticas-div-5{
    
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 12px;
    padding: 12px;
}

.estadisticas-div-6{
    padding: 2px;
}

.estadisticas-div-7{
    margin: 2px;
}

.estadisticas-div-8{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 12px;
}

.estadisticas-image-1{
    width:80px;
    height:80px;
    object-fit:cover;
    border-radius:50%;
    border:3px solid dodgerblue  ;
    position: relative;
}

.estadisticas-label-1{
    width: 100%;
    color: white;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
