.registrar-div-1{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
   margin: 12px;
   padding: 5px;

}

.registrar-nav-1{
    background-image: linear-gradient( 178.5deg,  rgba(100,129,255,1) 2.7%, rgba(2,36,184,1) 98% );   
    display: flex;
    flex-direction: row;
    height: auto;
    width: auto;
    justify-content: center;
    align-self: center;
    padding: 5px;
    margin-bottom: 5px;
    border-radius: 2%;
    opacity: 0;
}

.registrar-nav-1:hover{
    opacity: 1;
}

.registrar-texto-1{
    color: white;
    font-size: 24px;
    margin: 3px;
    padding: 3px;
    justify-content: center;
    display: flex;
    justify-self: center;
    align-self: center;
}

.registrar-texto-1:hover{
    color: black;
    font-weight: 600;
    background-color: dodgerblue;
    border-radius: 10%;
}

