.login-div-1{
    align-items: center;
    justify-content: center;
    position: absolute;
    right: 0px;

    width: 25%;
    margin: 24px;
    padding-left: 10px;
    padding-top: 24px;
    padding-right: 10px ;
    padding-bottom: 24px;

    opacity: 0.9;
    border: white;
    border-width: 2px;
    border-style: groove;
    border-color: lightblue;
    border-radius: 2%;
}

.login-texto-1{
    color: white;
    font-style: italic;
    justify-content: center;
    display: flex;
}

.login-texto-2{
    color: white;
    font-style: italic;
    justify-content: center;
    display: flex;
}

.login-texto-3{
    color: rgb(255, 248, 248);
    font-size: 20px;
    font-weight: 400;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
}

