.inicio-div-1{
    display: flex;
    flex-direction: column;
}

.inicio-nav-1{
    background-image: linear-gradient( 178.5deg,  rgba(100,129,255,1) 2.7%, rgba(2,36,184,1) 98% );   
    display: flex;
    flex-direction: row;
    height: auto;
    width: auto;
    justify-content: center;
    align-self: center;
    padding: 5px;
    margin-bottom: 5px;
    border-radius: 2%;
    opacity: 0;
}

.inicio-nav-1:hover{
    opacity: 1;
}

.inicio-texto-1{
    color: white;
    font-size: 24px;
    margin: 3px;
    padding: 3px;
    justify-content: center;
    display: flex;
    justify-self: center;
    align-self: center;
}

.inicio-texto-1:hover{
    color: black;
    font-weight: 600;
    background-color: dodgerblue;
    border-radius: 10%;
}

.inicio-texto-2{
    color:white;
    font-size: 12px;
    background: #0f0c29;
    margin: 10%;
    
}

.inicio-texto-3{
    color:white;
    font-size: 52px;
    display: flex;
    justify-content: center;
}

.inicio-texto-4{
    
  color:goldenrod;
  display: flex;
  font-size: 120px;
  font-weight: 500;
  justify-content: center;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  
}

.inicio-texto-5{
    color: black;
    font-size: 20px;
    font-weight: 600;
    background-color: dodgerblue;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
}

.inicio-div-perfil{
    margin: 12px;
    padding: 2px;
    position: fixed;
    top: 0;
    right: 0;
}

.inicio-image-1{
  width:80px;
  height:80px;
  object-fit:cover;
  border-radius:50%;
  border:3px solid dodgerblue  ;
  position: relative;
}

.inicio-div-2{
    display: flex;
    align-items: center;
    justify-content: center;
    justify-self: center;
    align-self: center;
    flex-direction: column;
    width: 90%;
    min-height: 60vh;
}

.inicio-div-3{
    padding: 10px;
    display: flex;
    flex-direction: column;
    margin: 12px;
    width: 300px;
}

.inicio-div-carousel-1{
    padding: 36px;
    opacity: 0.90;
    border-radius: 5%;
    background: #000428;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #004e92, #000428);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #004e92, #000428); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    width: 700px;
    margin: 12px;
}
.inicio-div-4{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}